.special__occasions__container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  margin-block: 2rem;
  /* border: 1px solid green; */
  padding-inline: 5rem;
  width: 100%;
}

.calendar__heading__container {
  display: flex;
  justify-content: space-between;
}

.calendar__container {
  display: flex;
  justify-content: end;
}

.emphasized {
  font-family: var(--ff-primary);
  font-weight: var(--fw-900);
  font-size: 24px;
  color: var(--clr-primary);
}

.form__container {
  display: flex;
  flex-direction: column;
}

.input__name,
.input__phone,
.input__email {
  display: flex;
  flex-direction: column;
  margin-bottom: 0.4rem;
  padding-block: 0.4rem;
}

input {
  padding: 0.4rem;
}

.calendar__heading p {
  font-weight: var(--fw-500);
}

@media (max-width: 800px) {
  .special__occasions__container {
    padding-inline: 0;
  }

  .calendar__heading__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
  }

  .calendar__heading h2,
  .calendar__heading p,
  .emphasized {
    text-align: center;
  }
}
